/*--------------------------------------------------------------
0.3  hero slider
--------------------------------------------------------------*/
.hero {
    position: relative;
    height: 800px;
    overflow: hidden;
}

.slide {
    position: relative;
}

.slide:after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    background: #000;
    opacity: .50;
}

.slide-inner{
    width: 100%;
    height: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.hx-slide-caption {
    z-index: 19;
    padding-bottom: 45px;
}

.hx-hero-style-1 .hx-slide-caption {
    z-index: 19;
    padding-bottom: 20px;
}

.hero .slide {
    height: 800px;
    position: relative;
    background-repeat: no-repeat;
    position: relative;
}

.hero .slide .slider-bg {
    display: none;
}

.hero .slide:focus {
    outline: none;
}

.hero .slide .container {
    height: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.hero .slick-prev,
.hero .slick-next {
    width: 50px;
    height: 50px;
    z-index: 10;
    transition: all .3s;
    background: rgba(255, 255, 255, .3);
}

.hero .slick-prev {
    left: 25px;
}

.hero .slick-prev:before {
    font-family: "FontAwesome";
    content: "\f104";
    opacity: 1;
    color: #fff;
}

.hero .slick-next {
    right: 25px;
}

.hero .slick-next:before {
    font-family: "FontAwesome";
    content: "\f105";
    opacity: 1;
    color: #fff;
}

.hero .slick-prev:hover,
.hero .slick-next:hover {
    background: #ed5217;
    border: none;
}

.hero .slick-prev:hover:before,
.hero .slick-next:hover:before {
    color: #fff;
}

.hero .slick-dots {
    bottom: 35px;
    color: red;
}

@media (max-width: 1920px) {

    .hx-hero-style-1 .slick-dots li,
    .hx-hero-style-2 .slick-dots li {
        display: none;
    }
}

.hero .slick-dots li {
    margin: 10px;
}

.slick-dots li.slick-active button:before {
    opacity: 1;
    color: #ed5217;
    border: 1px solid #ed5217;
    border-radius: 100%;
}

.slick-dots li button:hover:before,
.slick-dots li button:focus:before {
    opacity: 1;
}

.slick-dots li button:before {
    font-size: 12px;
    line-height: 25px;
    width: 25px;
    height: 25px;
    color: #fff;
    opacity: 1;
}


